import {Link, Route, Routes, useLocation, redirect, Navigate} from "react-router-dom";
import AppBar from "./static/AppBar";
import Main from "./pages/main/Main";
import Footer from "./static/Footer";
import About from "./pages/about/About";
import NutritionPage from "./pages/nutrition/NutritionPage";
import {Script} from "react-load-script";
import $ from 'jquery';
import WOW from 'wowjs';
import {useEffect} from "react";
import ProductsView from "./pages/product/ProductsView";
import ProductDetail from "./pages/product/ProductDetail";

function App() {
    useEffect(() => {
        const wow = new WOW.WOW();
        $('.fixed-top').css('top', $('.top-bar').height());
        $(window).scroll(function () {
            if ($(this).scrollTop()) {
                $('.fixed-top').addClass('bg-dark').css('top', 0);
            } else {
                $('.fixed-top').removeClass('bg-dark').css('top', $('.top-bar').height());
            }
        });

        $(window).scroll(function () {
            if ($(this).scrollTop() > 300) {
                $('.back-to-top').fadeIn('slow');
            } else {
                $('.back-to-top').fadeOut('slow');
            }
        });
        $('.back-to-top').click(function () {
            $('html, body').animate({scrollTop: 0}, 1500, 'easeInOutExpo');
            return false;
        });
        $(document).ready(() => {
            wow.init();
        });
    });
    return (
        <>
            <AppBar/>
            <Routes>
                <Route path={"/"} element={<Main/>}/>
                <Route path={"/about"} element={<About/>}/>
                <Route path={"/nutrition"} element={<NutritionPage/>}/>
                <Route path={"/products"} element={<ProductsView/>}/>
                <Route path={"/detail"} element={<Navigate to={"/products"}/>}/>
                <Route path={"/detail/:pId"} element={<ProductDetail/>}/>
            </Routes>
            <Footer/>
            <a href="#" className="btn btn-lg btn-primary btn-lg-square rounded-circle back-to-top"><i
                className="bi bi-arrow-up"></i></a>
        </>
    );
}

export default App;
